import { useEffect, useState, useRef } from 'react';
import './App.css';

function App() {

  const [data, setData] = useState([]);

  const carousel = useRef(null)

  useEffect(() => {
    fetch('http://localhost:3000/static/shoes.json')
      .then((response) => response.json())
      .then(setData);
  }, [])

  const handleRigthClick = (e) => {
    e.preventDefault()
    //console.log(carousel.current.offsetWidth)
    carousel.current.scrollLeft += carousel.current.offsetWidth
  }

  const handleLeftClick = (e) => {
    e.preventDefault()
    //console.log(carousel.current.offsetWidth)
    carousel.current.scrollLeft -= carousel.current.offsetWidth
  }

  if (!data || !data.length) return null;

  return (

    <div className='Logo'>
      <img src='./static/images/super-shoes.png' alt='Logo' />
      <div className='container'>

        <button className='leftButton' onClick={handleLeftClick}><img src="/static/images/216151_right_chevron_icon.png" alt='Scroll Left' /></button>

        <div className='carousel' ref={carousel}>

          {data.map((item) => {

            const { id, name, oldPrice, price, image } = item;

            return (
              <div className='item' key={id}>
                <div className='image'>
                  <img src={image} alt={name} />
                </div>
                <div className='info'>
                  <span className='name'>{name}</span>
                  <span className='oldPrice'>U$ {oldPrice}</span>
                  <span className='price'>U$ {price}</span>
                </div>
              </div>
            );

          })}
        </div>

        <button className='rightButton' onClick={handleRigthClick} ><img src="/static/images/216151_right_chevron_icon.png" alt='Scroll Rigth' /></button>

      </div>
    </div>
  );
}

export default App;
